.explore-modal {
  width: 90%;
  max-width: 1000px;
  border-radius: 18px;
  padding: 20px 24px;

  background-image: linear-gradient(
      rgba(255, 255, 255, 0.94),
      rgba(255, 255, 255, 0.94)
    ),
    url("/images/background.png");

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.explore-modal {
  width: 90%;
  max-width: 1000px;
  background: #fff;
  border-radius: 18px;
  padding: 20px 24px;
}

.explore-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.close-btn {
  border: none;
  background: none;
  font-size: 22px;
  cursor: pointer;
}

.explore-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(110px, 1fr));
  gap: 16px;
}

.explore-card {
  border-radius: 14px;
  padding: 14px 10px;
  text-align: center;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.explore-card:hover {
  transform: translateY(-3px);
}

.icon-box {
  width: 46px;
  height: 46px;
  background: #fff;
  border-radius: 12px;
  margin: 0 auto 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-box img {
  width: 26px;
  height: 26px;
  object-fit: contain;
}

.explore-card p {
  font-size: 13px;
  margin: 0;
  font-weight: 500;
}

@media (max-width: 768px) {
  .explore-modal {
    max-height: 90vh;
    overflow-y: auto;
  }
}
